$facebook-group: ('facebook', $facebook, $facebook-2x, );
$instagram-group-name: 'instagram';
$instagram-group: ('instagram', $instagram, $instagram-2x, );
$instagram-black-group-name: 'instagramBlack';
$instagram-black-group: ('instagramBlack', $instagram-black, $instagram-black-2x, );
